const mockVal = (str, repeat = 1) => ({
  value: str.repeat(repeat),
});

function HomeSearchBox(props) {
  const [name, setName] = useState('');

  const [value, setValue] = useState('');
  const [activeSuggestion, setActiveSuggestion] = useState(0);
  const [filteredSuggestions, setFilteredSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [userInput, setUserInput] = useState('');

  const onChange = e => {
    const { suggestions } = props;
    const userInput = e.currentTarget.value;
  
    const filteredSuggestions = suggestions.filter(
      suggestion =>
        suggestion.toLowerCase().indexOf(userInput.toLowerCase()) > -1
    );

    setActiveSuggestion(0)
    setFilteredSuggestions(filteredSuggestions)
    setShowSuggestions(true)
    setUserInput(e.currentTarget.value)

  };

  const onClick = e => {
    setActiveSuggestion(0)
    setFilteredSuggestions([])
    setShowSuggestions(false)
    setUserInput(e.currentTarget.innerText)
  };

 const  onKeyDown = e => {
    
    if (e.keyCode === 13) {
    setActiveSuggestion(activeSuggestion + 1)
    setShowSuggestions(false)
    setUserInput(filteredSuggestions[activeSuggestion])
     
    } else if (e.keyCode === 38) {
      if (activeSuggestion === 0) {
        return;
      }
    setActiveSuggestion(activeSuggestion - 1)

     /*  this.setState({ activeSuggestion: activeSuggestion - 1 }); */
    }
    // User pressed the down arrow, increment the index
    else if (e.keyCode === 40) {
      if (activeSuggestion - 1 === filteredSuggestions.length) {
        return;
      }
    setActiveSuggestion(activeSuggestion + 1)

     /*  this.setState({ activeSuggestion: activeSuggestion + 1 }); */
    }
  };

  let suggestionsListComponent;

  if (showSuggestions && userInput) {
    if (filteredSuggestions.length) {
      suggestionsListComponent = (
        <ul class="suggestions">
          {filteredSuggestions.map((suggestion, index) => {
            let className;
  
            // Flag the active suggestion with a class
            if (index === activeSuggestion) {
              className = "suggestion-active";
            }
            return (
              <li className={className} key={suggestion} onClick={onClick}>
                {suggestion}
              </li>
            );
          })}
        </ul>
      );
    } else {
      suggestionsListComponent = (
        <div class="no-suggestions">
          <em>No suggestions available.</em>
        </div>
      );
    }
  }

 
  const [options, setOptions] = useState([]);

  const handleSearch = (value) => {
    setOptions(value ? searchResult(value) : []);
  };

  const onSelect = (value) => {
    console.log('onSelect', value);
  };

 /*  const onChange = (data) => {
    setValue(data);
  }; */

  function getRandomInt(max, min = 0) {
    return Math.floor(Math.random() * (max - min + 1)) + min; // eslint-disable-line no-mixed-operators
  }
  

  const searchResult = (query) =>
  new Array(getRandomInt(5))
    .join('.')
    .split('.')
    .map((_, idx) => {
      const category = `${query}${idx}`;
      return {
        value: category,
        label: (
          <div
            style={{
              display: 'flex',
              justifyContent: 'space-between',
            }}
          >
            <span>
              Found {query} on{' '}
              <a
                href={`https://s.taobao.com/`}
                /* href={`https://s.taobao.com/search?q=${query}`} */
                target="_blank"
                rel="noopener noreferrer"
              >
                {category}
              </a>
            </span>
            <span>{getRandomInt(200, 100)} results</span>
          </div>
        ),
      };
    });


  

  const submitHandler = (e) => {
    e.preventDefault();
    name === "" ? props.history.push(`/reservations`):
    props.history.push(`/reservation/prestation/${name}`);
  };

  return (
    <SearchBox onSubmit={submitHandler}>
  <input 
  type="text" 
 /*  onChange={onChange} */
  /* onChange={(e) => setName(e.target.value)} */
  /* onKeyDown={onKeyDown} */
  value={name}
  placeholder="Quelles genre de service procurez-vous ?"
  />
   {/* {suggestionsListComponent} */}
  {/* <br /> */}
 {/*  <AutoComplete
        options={options}
        style={{
          width: 200,
        }}
        onSelect={onSelect}
        onSearch={onSearch}
        placeholder="input here"
      />
      <br /> */}
      
      {/* <AutoComplete
         dropdownMatchSelectWidth={252}
         style={{
           width: 300,
         }}
        options={options}
      onSelect={onSelect}
      onSearch={handleSearch}
      >
        <Input.Search size="large" placeholder="input here" enterButton />
      </AutoComplete> */}
  <button type="submit">
    <FaSearch/>
  </button>
</SearchBox>
 

  )
}

export default withRouter(HomeSearchBox)
