export const createOrganizer = (organizer) => async (
  dispatch,
  getState
  ) => {
  dispatch({ type: CREATE_ORGANIZER_REQUEST});
  const {
    userSignin: { userInfo },
  } = getState();
  try {  
    const { data } = await axios.get(`/api/users/organizers`, 
    organizer,
    {
      headers: {
        Authorization: `Bearer ${userInfo.token}`,
      },
    });
    dispatch({ type: CREATE_ORGANIZER_SUCCESS, payload: data.organizer });
  } catch (error) {
    dispatch({
      type: CREATE_ORGANIZER_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};
